/* font-family: 'Poppins', sans-serif; */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&family=Poppins:ital,wght@0,400;0,700;1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@200;300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600&display=swap');
/* font-family: 'Montserrat', sans-serif; */
/* font-family: 'Raleway', sans-serif; */
/* font-family: 'Dancing Script', cursive; */
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* overflow-x: hidden; */
    
}
body{
    overflow-x: hidden;
}
/*------------navbar css-------------*/
.navbar{
    font-family: 'Montserrat', sans-serif;
    font-size: 17px;
    line-height: 26px;
    text-transform: uppercase;
    box-shadow: 0px 2px 2px -2px rgb(234, 234, 234);
}
.nav-item::before{
    content: "";
    margin: 5px;
    position: absolute;
    /* top: 0; */
    /* left: 0; */
    width: 0;
    height: 2px; 
    background-color: #000; 
    transition: width 0.3s ease; 
}
.nav-item:hover:before{
    width: 50px;
}
.active{
    
    font-weight: 500;
}

.kam{
    font-family: 'Dancing Script', cursive;
    font-weight: bolder;
}
  
  
.navbar-brand{
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
    letter-spacing: 1px;
    align-items: center;
}
.footer{
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    font-family: 'Montserrat', sans-serif; 
    letter-spacing: 0px;
    font-size: small;
    height: 100px;
    padding-top: 15px;

}
.footer a{
    text-decoration: none;
    color: rgb(0, 0, 0);
    padding: 10px;
}
.footer a:hover{
    border-bottom: 1px solid aliceblue;
}

/*================page not found==================*/
.pnf{
    display: flex;
    min-height: 65vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.pnf-title{
    font-size: 100px;
    font-weight: 700;
}
.pnf-heading{
    font-weight: normal;
    font-family: 'Lora', serif;
}
.pnf-button{
    color: black;
    border: 1px solid black;
    text-decoration: none;
    padding: 10px;
    margin-top: 10px;
    transition: 0.2s ease-in-out;
    font-family: 'Montserrat', sans-serif; 
}
.pnf-button:hover{
    background-color: black;
    color: white;
}
/*====================Contact=======================*/
.con-heading{
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
}
.queryP{
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;

}
.small{
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;

}
.send-button{
    color: white;
    background-color: black;
    text-decoration: none;
    padding: 10px;
    margin-top: 10px;
    
    font-family: 'Montserrat', sans-serif; 
}
textarea{
    border-radius: 0%;
    padding: 5px;
}
/*==================ABOUT===================*/
.about-text{
    font-family: 'Montserrat', sans-serif; 
    margin-top: 1.5%;
}
.about-desc-text{
    font-family: 'Poppins', sans-serif;
    letter-spacing: 2.5px;
}
/*==================REGISTER===================*/
.register{
    display: flex;
  
    
    justify-content: center;
    align-items: center;
}
 .spcon{
    padding: 3%;
   
    width: 100vw;
    background-color: aliceblue;
}

.innerCon{
    background-color: rgb(255, 255, 255);
    
    width: 100vw;
    overflow: hidden;
   
}

.col-lg{
    padding-left: 0% !important;
    padding-right: 0% !important;
}

.innerCon .left img{
    width: 100%;
  
 
}



.innerCon .right{
    padding:2% 2% ;
}
.innerCon .right h3{

    font-family: 'Montserrat', sans-serif;
    font-weight: bolder;
}
.form-label{
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
}
.form-control{
    outline: none;
    border: none;
    border-top:0px !important;
    border-left: 0px !important;
    border-right: 0px !important;
    border-radius: 0% !important;
    border-bottom: 2px solid #4A00E0  !important;
    
}

.form-control{
    box-shadow: none !important;
    padding: 10px 0px;
}

.submit{
    width: 100%;
    background: linear-gradient(to right,#46b3ee, #8E2DE2);
    color: white;
    height: 50px;
    border-radius: 0%;
    font-family: 'Montserrat', sans-serif;

    
}
.submit:hover{
    color: white !important;
}
.submit-forgot{
    width: 100%;
    background: linear-gradient(to right,#acdbf4, #d1a5f7);
    -webkit-text-fill-color: #8E2DE2;
    height: 50px;
    border-radius: 0%;
    font-family: 'Montserrat', sans-serif;
   
}

.warn{

    width: 100%;
    background: white;
    color: black ;
    border: none;
    outline: none;
    height: 50px;
    border-radius: 0%;
    font-family: 'Montserrat', sans-serif;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.th{
    width: 100000px !important;
}
/*======================*/
.product-link{
    text-decoration: none;
}
.search{
    border: none !important;
    outline: none !important;
    padding-left: 5px;
}
.dropdown-menu{
    width: 300px;
    height: 250px;
    position: fixed;
    border-radius: 0% !important;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
     transform: translateX(-50px);
    
}
.dropdown:hover .dropdown-menu{
    display: block;
    position: absolute;
    opacity:1;

}
/*======================================
GREET
=========================================*/
.image:hover{
    transform: scale(1.1);
    transition: transform 0.7s ease-in-out;

}
.image:hover .img-text{
    opacity: 1;
}
.inner{
    overflow: hidden;
    position: relative;
}
.text{
    overflow: hidden;
}
.image{
    overflow: hidden;
    cursor: pointer;
}
.img-text{
position: absolute;
  bottom: 0;
  font-family: 'Montserrat', sans-serif;
  left: 0;
  width: 100%;
  height: 70px;
  background-color: rgb(236, 236, 236); /* White background with opacity */
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0; /* Initially hidden */
  transition: opacity 0.3s;
}
.filter-cat{
    font-family: 'Raleway', sans-serif;
}
.filter-price{
    font-family: 'Raleway', sans-serif;

}
.cate{
    
    font-family: 'Montserrat', sans-serif;
}
.clear{
    border-radius: 0%;
    background-color: #000;
    color: white;
    font-family: 'Montserrat', sans-serif;

}
.clear:hover{
    background-color: #000;
    color: white; 
}
.card{
    
    border-radius: 0% !important;
    border: none !important;
    outline: none !important;
}
.card-img-top{
    height: 450px;
    width: 300px;
    border-radius: 0% !important;

}
.detail{
    background-color: transparent;
    outline: 1px solid black;
    border-radius: 5%;
    font-family: 'Montserrat', sans-serif;
    width: 100px;

}
.detail:hover{
    width: 150px;
    transition: width 3s ease-in-out;
    background-color: black;
    color: white;
    /* transition: width 1s; */

}

  
.cart{
    background-color: #1f1f1f;
    color: white;
    border-radius: 0%;
    font-family: 'Montserrat', sans-serif;
    height: 40px;
}
.loading{
    background-color: transparent;
    color: #000;
    border: 1px solid black;
    border-radius: 0%;
}
/*==========================================
Detail Page
========================*/
.det-cart{
    background-color: rgb(27, 26, 26);
    color: white;
    width: 300px;
    border-radius: 25px;
    height: 50px;
    font-family: 'Montserrat', sans-serif !important;

    box-shadow: rgba(186, 186, 193, 0.25) 0px 30px 60px -12px inset, rgba(182, 168, 168, 0.3) 0px 18px 36px -18px inset;
}
.det-cart:hover{
    background-color: #000;
    color: white;
}
.good{
    background-color: green;
    color: white;
    border-radius: 0%;
}

.care-foot{
    font-weight: 550;
    font-size: medium;
}
.infos{
    background-color: whitesmoke;
}
@media only screen and (max-width: 768px)  {
    .info{
        justify-content: left;
    }
    
}
